import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        unLoginedMoreMenus: {
            login: "登录"
        },
        isLogined: false, //未登录
        isShowMore: false, //actionsheet默认不显示
        loginname: '', //登录名默认为空
        accesstoken: '',
        LoginedMoreMenus: {
            center: "个人中心",
            msg: "消息通知",
            logout: "登出"
        },
        topics: [
            { name: "all", desc: "全部" },
            { name: "good", desc: "精华" },
            { name: "share", desc: "分享" },
            { name: "ask", desc: "问答" },
            { name: "job", desc: "招聘" }
        ]
    },
    getters: {
        moreMenus: function (state) {
            if (state.isLogined) {
                return state.LoginedMoreMenus//已登录
            } else {
                return state.unLoginedMoreMenus//未登录
            }
        }
    },
    mutations: {
        mutationIsShowMore: function (state, payload) {
            //state表示当前store对象的state数据，payload表示函数调用
            state.isShowMore = payload
        },
        mutationIsLogin: function (state) {
            state.isLogined = true
        },
        mutationLoginname: function (state, payload) {
            state.loginname = payload
        },
        mutationAccesstoken: function (state, payload) {
            state.accesstoken = payload
        },
        mutationLogout: function (state) {
            state.isLogined = false
            state.loginname = ''
            state.accesstoken = ''
        }
    },

    actions: {}
})

export default store



