
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState } from "vuex";
export default {
name: "topiclist",
props: ["name"], //第二种方式
computed: {
...mapState(["topics"])
},
data: function() {
return {
lists: [],
page: 1,
limit: 10,
pullupConfig: {

content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中....."
}
};
},
components: {
Cell,
Group,
Scroller,
Tab,
TabItem
},
methods: {
routeto: function(index) {


this.$router.push("/topiclist/" + this.topics[index].name);
},
loadMoreData() {
var app = this;
this.page = this.page + 1;
this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
})
.then(function(res) {

for (var i = 0; i < res.data.data.length; i++) {
app.lists.push(res.data.data[i]);
}

app.$refs.pullup.donePullup();
});
}
},



beforeRouteEnter: function(to, from, next) {


next(function(vm) {
vm.$http
.get("/topics", {
params: {
tab: to.params.name, //动态路由获取方式
page: vm.page,
limit: vm.limit
}
})
.then(function(res) {

if (res.data.success) {
vm.lists = res.data.data;
}
});
});
},
beforeRouteUpdate: function(to, from, next) {
var app = this;
this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(function(res) {
if (res.data.success) {

app.lists = res.data.data;
}
});
next();
}
};



