
import { mapState, mapGetters, mapMutations } from "vuex";
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
name: "app",
data: function() {
return {};
},
computed: {
...mapGetters(["moreMenus"]),
...mapState([
"isLogined",
"isShowMore",
"LoginedMoreMenus",
"unLoginedMoreMenus"
]),
isShowMore: {
get: function() {
return this.$store.state.isShowMore;
},
set: function(nval) {
this.$store.commit("mutationIsShowMore", nval);
}
},
moreMenus() {


if (this.isLogined) {
return this.LoginedMoreMenus;
} else {
return this.unLoginedMoreMenus;
}
}
},
mounted: function() {
var loginStatus = window.localStorage.getItem("loginStatus");
if (loginStatus) {

this.mutationIsLogin();
this.mutationLoginname(window.localStorage.getItem("loginname"));
this.mutationAccesstoken(window.localStorage.getItem("accesstoken"));
}
},
methods: {
...mapMutations([
"mutationIsLogin",
"mutationLoginname",
"mutationAccesstoken"
]),
hand: function(menuKey, menuItem) {


this.$router.push("/" + menuKey);
},
showMoreMenus: function() {
this.isShowMore = true;

}
},
components: {
XHeader,
Actionsheet
}
};



