import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Msg from '../components/Msg'
import Logout from '../components/Logout'
import Center from '../components/Center'
import TopicList from '@/components/TopicList'
import Topic from '../components/Topic'
import User from '../components/User'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', redirect: '/topiclist/all'  //重定向，网页上默认显示全部 all
    },
    {
      path: '/topiclist/:name', //动态路由 参数name
      name: 'TopicList',
      component: TopicList,
      props: true //解耦 第二种方式
    },
    {
      path: '/topic/:id',
      component: Topic,
      props: true
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/center',
      component: Center
    },
    {
      path: '/logout',
      component: Logout
    },
    {
      path: '/msg',
      component: Msg
    },
    {
      path: '/user/:loginname',
      component: User,
      props: true
    }
  ]
})



